.bt{
    width: 200px;
    height: 80px;
    position: relative;
    background-image: url('./image/btn1.png');
    background-color: transparent;
    background-repeat: no-repeat;
    background-position: center;
    background-size: auto 100%;
    border: none;
    
    font-size: 30px;
    line-height: 30px;
    color: #fff;
}
